function iniciarApp(){scrollNav(),navegacionFija(),crearGaleria()}function navegacionFija(){const e=document.querySelector(".header"),t=document.querySelector("body"),n=document.querySelector(".sobre-festival");window.addEventListener("scroll",(function(){n.getBoundingClientRect().top<0?(console.log("pasamos el elemnto"),e.classList.add("fijo"),t.classList.add("body-scroll")):(console.log("aun no"),e.classList.remove("fijo"),t.classList.remove("body-scroll"))}))}function scrollNav(){document.querySelectorAll(".navegacion-principal a").forEach(e=>{e.addEventListener("click",(function(e){e.preventDefault();const t=e.target.attributes.href.value;document.querySelector(t).scrollIntoView({behavior:"smooth",block:"start"})}))})}function crearGaleria(){const e=document.querySelector(".galeria-imagenes");for(let t=1;t<=12;t++){const n=document.createElement("PICTURE");n.innerHTML=`<source srcset="build/img/thumb/${t}.avif" type="image/avif">\n\t\t\n<source srcset="build/img/thumb/${t}.webp" type="image/webp">\n\t\t\n<img width="200" height="300" src="build/img/thumb/${t}.jpg" alt="Imagen Galeria">`,n.onclick=function(){mostarImagen(t)},e.appendChild(n)}}function mostarImagen(e){const t=document.createElement("PICTURE");t.innerHTML=`<source srcset="build/img/grande/${e}.avif" type="image/avif">\n\t\n<source srcset="build/img/grande/${e}.webp" type="image/webp">\n\t\n<img width="200" height="300" src="build/img/grande/${e}.jpg" alt="Imagen Galeria">`;const n=document.createElement("DIV");n.appendChild(t),n.classList.add("overlay"),n.onclick=function(){c()};const o=document.createElement("P");function c(){document.querySelector("BODY").classList.remove("fijar-body"),n.remove()}o.textContent="x",o.classList.add("btn-cerrar"),o.onclick=function(){c()};const i=document.querySelector("BODY");i.classList.add("fijar-body"),n.appendChild(o),i.appendChild(n)}document.addEventListener("DOMContentLoaded",(function(){iniciarApp()}));